import React, { useEffect, useState } from "react";
import {
  DetailViewWrapper,
  LegThumbnails,
  LegWrapper,
  CharacteristicsWrapper,
  UploadWrapper,
  TorsoThumbnail,
  Progress,
} from "./styled";
import { Button } from "../Misc/Button";
import { Link } from "gatsby";
import ImageWithSkeleton from "../Misc/ImageWithSkeleton";
import { HufschuheFuer } from "./HufschueheFuer";
import { Checkmark } from "../Misc/Checkmark";
import { markCompleted } from "../../../services/markCompleted";
import axios from "axios";
import routes from "../../../constants/routes";
import store from "../../../redux/store";
import Logo from "../Misc/Logo";
import { ProfilePicWrapper } from "../Misc/ProfilePicWrapper";
import { setHorses } from "../../../redux/horses/action";
import { setError } from "../../../redux/appState/action";
import { HorseHead } from "../../../assets/icons/HorseHead";

export default function DetailViewComponent({
  horse,
  cms,
  patchSelectedHorse,
  cmsHufschuheFuer,
  patchHorse,
  initialState,
  user,
}) {
  // this is a dynamic route, no static build
  // if (!pferde) return "";
  const [state, setState] = useState(
    initialState || {
      Hufschuhe_Fuer: "",
    }
  );
  const [legs, setLegs] = useState({});
  const [message, setMessage] = useState("");
  const [initial, setInitial] = useState(true);
  const [canSend, setCanSend] = useState(false);

  useEffect(() => {
    switch (state.Hufschuhe_Fuer) {
      case cmsHufschuheFuer.Hufschuhe_Fuer.Optionen[0]: {
        setLegs({
          vr: {
            identifier: cms.VR,
            leg: "leg0",
            completed: horse?.images?.[`leg${0}`],
          },
          vl: {
            identifier: cms.VL,
            leg: "leg1",
            completed: horse?.images?.[`leg${1}`],
          },
        });
        break;
      }
      case cmsHufschuheFuer.Hufschuhe_Fuer.Optionen[1]: {
        setLegs({
          vr: {
            identifier: cms.VR,
            leg: "leg0",
            completed: horse?.images?.[`leg${0}`],
          },
          vl: {
            identifier: cms.VL,
            leg: "leg1",
            completed: horse?.images?.[`leg${1}`],
          },
          hr: {
            identifier: cms.HR,
            leg: "leg2",
            completed: horse?.images?.[`leg${2}`],
          },
          hl: {
            identifier: cms.HL,
            leg: "leg3",
            completed: horse?.images?.[`leg${3}`],
          },
        });
        break;
      }
      case cmsHufschuheFuer.Hufschuhe_Fuer.Optionen[2]: {
        setLegs({
          hr: {
            identifier: cms.HR,
            leg: "leg2",
            completed: horse?.images?.[`leg${2}`],
          },
          hl: {
            identifier: cms.HL,
            leg: "leg3",
            completed: horse?.images?.[`leg${3}`],
          },
        });
        break;
      }
      default: {
        setLegs({});
        break;
      }
    }
  }, [state]);

  function publish() {
    if (horse.inquiry_at) {
      return;
    }

    if (!canSend) {
      setMessage(cms.Alle_Felder);
      return;
    }

    patchHorse({ inquiry_at: Date.now() });

    axios({
      method: "post",
      url: routes.webhooks.publish,
      headers: {
        Authorization: `Bearer ${user.tokenManager.jwt}`,
        // "Content-Type": "multipart/form-data",
      },
      data: {
        func: "publish",
        userid: store.getState().auth.user.id,
        horseid: horse.id,
      },
    })
      .then((result) => console.log(result))
      .catch((err) => console.error(err, err.message));
  }

  useEffect(() => {
    if (initial) {
      setInitial(false);
      return;
    }
    patchHorse({
      Hufschuhe_Fuer: state.Hufschuhe_Fuer,
    });
  }, [state]);

  // useEffect(() => {
  //   console.log(canSend);
  //   const completed = markCompleted(horse);
  //
  //   console.log("STEP 0");
  //
  //   if (
  //     !completed.Steckbrief_Besonderheiten ||
  //     !completed.Steckbrief_Besonderheiten ||
  //     !completed.torso
  //   ) {
  //     setCanSend(false);
  //     return;
  //   }
  //   console.log("STEP1");
  //
  //   let missing = false;
  //   Object.keys(legs).forEach((key) => {
  //     const leg = legs[key].leg;
  //     if (!completed[leg][1] || !completed[leg][2] || !completed[leg][3]) {
  //       missing = true;
  //     }
  //   });
  //
  //   console.log("STEP2");
  //
  //   if (missing) {
  //     setCanSend(false);
  //   } else {
  //     setCanSend(true);
  //   }
  //
  //   console.log(canSend);
  // }, []);

  return (
    <DetailViewWrapper>
      {/*Pferd Meta*/}
      {/*Data that is not available at build-time must be optional*/}
      {/*Or uncomment the directive above*/}

      {/*<section>*/}
      {/*  <Logo />*/}
      {/*</section>*/}

      <Progress>
        <div className="breadcrumbs">
          <ul>
            <li>
              <Link
                to="#characteristics"
                className={
                  horse?.completed?.Steckbrief_Allgemeines &&
                  horse?.completed?.Steckbrief_Besonderheiten
                    ? "completed"
                    : ""
                }
              >
                <i className="fa fa-shopping-bag" /> Steckbriefe
              </Link>
            </li>
            <li>
              <Link
                to="#torso-pic"
                className={horse?.completed?.allFotos ? "completed" : ""}
              >
                <i className="fa fa-cart-plus" /> Fotos
              </Link>
            </li>
            <li>
              <Link to="#inquiry">
                <i className="fa fa-credit-card-alt" /> Senden
              </Link>
            </li>
          </ul>
        </div>
      </Progress>

      <section className={"section-profile-pic"}>
        <ProfilePicWrapper className={"large"}>
          {horse?.profilePicture ? (
            <img src={horse.profilePicture} alt={"Pferd Profilbild"} />
          ) : (
            <HorseHead />
          )}
        </ProfilePicWrapper>
      </section>

      {/*Steckbriefe*/}
      <CharacteristicsWrapper id={"characteristics"}>
        <h3>{cms.Steckbriefe}</h3>
        <Link to={routes.factFileGeneral(horse.id)}>
          {cms.Allgemeines}
          <div className={"checkmark"}>
            <Checkmark
              completed={horse?.completed?.["Steckbrief_Allgemeines"]}
            />
          </div>
        </Link>

        <Link
          className={"secondary"}
          to={routes.factFileParticularities(horse.id)}
        >
          {cms.Besonderheiten}{" "}
          <div className={"checkmark"}>
            <Checkmark
              completed={horse?.completed?.["Steckbrief_Besonderheiten"]}
            />
          </div>
        </Link>
      </CharacteristicsWrapper>

      {/*Picture Torso*/}
      <p style={{ textAlign: "center" }}>
        Über die folgenden Symbole gelangst du in den Kamera-Modus, um Bilder
        deines Equiden und seiner Hufe aufzunehmen. Klicke auf die Symbole, und
        eine genaue Anweisung anzuzeigen und die Aufnahme zu starten.
      </p>

      <TorsoThumbnail id={"torso-pic"}>
        <h3>{cms.Torso_Bild}</h3>
        <Link
          onClick={() => patchSelectedHorse("torso", 0)}
          to={`photo-guide?target=torso&pos=0`}
          className={"thumbnail-link"}
        >
          <ImageWithSkeleton
            image={
              cms.Thumbnails.Koerper.localFile.childImageSharp.gatsbyImageData
            }
            className={"thumbnail"}
            // showCheckmark={true}
            checked={!!horse?.completed?.["torso"]}
          />
        </Link>
      </TorsoThumbnail>

      <HufschuheFuer
        state={state}
        setState={setState}
        cms={cmsHufschuheFuer}
        disabled={!!horse?.inquiry_at}
      />

      {/*Leg Thumbnails*/}
      <LegThumbnails>
        {Object.keys(legs).map((key, index) => {
          const legIdentifier = legs[key].identifier;
          const leg = legs[key];
          return (
            <LegWrapper key={"tn-" + legIdentifier + index}>
              <p className={"identifier"}>{legIdentifier}</p>
              <Link
                to={`photo-guide`}
                onClick={() => patchSelectedHorse(legs[key].leg, 1)}
                className={"thumbnail-link"}
              >
                <ImageWithSkeleton
                  image={
                    cms.Thumbnails.Fessel.localFile.childImageSharp
                      .gatsbyImageData
                  }
                  className={"thumbnail"}
                  // showCheckmark={true}
                  checked={leg.completed?.[1]}
                />
              </Link>

              <Link
                to={`photo-guide`}
                onClick={() => patchSelectedHorse(legs[key].leg, 2)}
                className={"thumbnail-link"}
              >
                <ImageWithSkeleton
                  image={
                    cms.Thumbnails.Hufbreite.localFile.childImageSharp
                      .gatsbyImageData
                  }
                  className={"thumbnail"}
                  // showCheckmark={true}
                  checked={leg.completed?.[2]}
                />
              </Link>

              <Link
                to={`photo-guide`}
                onClick={() => patchSelectedHorse(legs[key].leg, 3)}
                className={"thumbnail-link"}
              >
                <ImageWithSkeleton
                  image={
                    cms.Thumbnails.Huflaenge.localFile.childImageSharp
                      .gatsbyImageData
                  }
                  className={"thumbnail"}
                  // showCheckmark={true}
                  checked={leg.completed?.[3]}
                />
              </Link>
            </LegWrapper>
          );
        })}
      </LegThumbnails>

      <UploadWrapper>
        <Button
          className={`primary ${
            !!horse?.inquiry_at || !canSend ? "inactive" : "active"
          }`}
          onClick={publish}
        >
          {cms.Verooeffentlichen}
        </Button>
        <p className={"upload-message"}>{message}</p>

        <p className={"inquiry"} id={"inquiry"}>
          <span> {!!horse?.inquiry_at ? cms.Veroeffentlicht_Am : ""}</span>
          <span>
            {!!horse?.inquiry_at
              ? new Date(horse?.inquiry_at).toLocaleString()
              : ""}
          </span>
        </p>
      </UploadWrapper>
    </DetailViewWrapper>
  );
}
