import { connect, useDispatch, useSelector } from "react-redux";
import DetailViewComponent from "./DetailView";
import { patchHorse, patchLocalHorse } from "../../../redux/horses/action";
import store, { RootState } from "../../../redux/store";
import {
  useGetHorseQuery,
  useUpdateHorseMutation,
} from "../../../redux/api/horseApi";
import React from "react";
import {
  patchSelectedHorse,
  SelectedHorse,
  setSelectedHorse,
} from "../../../redux/appState/appStateSlice";

type DetailViewProps = {
  horseId: string;
  cms: any;
  cmsHufschuheFuer: any;
  images: any;
};

export const DetailViewData = ({
  horseId,
  cms,
  cmsHufschuheFuer,
  images,
}: DetailViewProps) => {
  const user = useSelector((state: RootState) => state.auth.user);
  const appState = useSelector((state: RootState) => state.appState);
  const dispatch = useDispatch();

  const { data: horseData, isLoading } = useGetHorseQuery(
    { userId: user?.id as string, horseId: horseId },
    {
      refetchOnMountOrArgChange: true,
      skip: !horseId,
    }
  );

  const [updateHorse, updateHorseResult] = useUpdateHorseMutation();

  const saveHorse = async (horse: any) => {
    await updateHorse({
      userId: user?.id as string,
      horseId: horseId,
      data: horse,
    });
  };

  if (isLoading) {
    return <span>Lädt..</span>;
  }

  return (
    <DetailViewComponent
      horse={horseData}
      cms={cms}
      patchSelectedHorse={(target: string, pos: number) =>
        dispatch(
          patchSelectedHorse({
            selectedHorse: {
              pos,
              target,
            },
          })
        )
      }
      cmsHufschuheFuer={cmsHufschuheFuer}
      patchHorse={saveHorse}
      initialState={horseData}
      user={user}
    />
  );
};
